body {
  background-color: $bg-color;
  border: 10px solid $border-color;
}

.container-960 {
  max-width: 960px;
  margin: 0 auto;
}

.principal {
  display: grid;
  grid-template-columns: 5fr 7fr;
  margin-top: 75px;
  padding: 20px;
  gap: 80px;
  margin-bottom: 150px;
}

.logotipo {
  max-width: 200px;
}

.img-principal {
  grid-column: 2;
  grid-row: 1/4;
  border: 10px solid $border-color;
}

.informacoes {
  max-width: 300px;
  div span {
    display: block;
    line-height: 30px;
    font-weight: bold;
    color: $primary-color;
    @include font($font-1, 18);
  }
}

.funcionamento {
  padding-bottom: 10px;
  border-bottom: 3px solid $primary-color;
  border-right: 3px solid $primary-color;
}

.endereco {
  padding-top: 10px;
  border-right: 3px solid $primary-color;
}

.telefone {
  color: $secondary-color;
  line-height: 30px;
  @include font($font-1, 18);
}

.detalhes {
  display: flex;
  gap: 50px;
  margin-left: 40px;

  div {
    width: 20px;
    height: 220px;
    background-color: $primary-color;
    transform: rotate(20deg);
  }
}
